.promo {
  padding-top: 32px;
  padding-bottom: 42px;

  background-image: url("../img/svg/zigzag-line.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;

  @media (min-width: $tablet-width) {
    padding-top: 40px;
    padding-bottom: 67px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 54px;
    padding-bottom: 12px;

    background: none;
  }
}

.promo__container {
  display: grid;

  gap: 16px;
  grid-template-columns: 1fr;

  @media (min-width: $tablet-width) {
    display: grid;

    gap: 37px 42px;
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width: $desktop-width) {
    position: relative;

    padding-bottom: 60px;
    padding-left: 226px;

    background: none;

    gap: 35px 60px;
    grid-template-columns: 1fr 525px;
  }

  &::after {
    @media (min-width: $desktop-width) {
      content: "";
      position: absolute;
      right: 50px;
      bottom: 0;
      left: 50px;
      z-index: 1;

      height: 23px;

      background-image: url("../img/svg/zigzag-line.svg");
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: contain;
    }
  }
}

.promo__title-container {
  @media (min-width: $tablet-width) {
    display: grid;
    align-items: center;

    gap: 10px;
    grid-column: 1/-1;
    grid-template-columns: 1fr 125px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 62px;

    grid-row: 1 / span 1;
  }
}

.promo__title {
  margin-bottom: 4px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    z-index: 1;
  }
}

.promo__type {
  display: block;

  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    line-height: 17px;
    text-align: center;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 70px;
    left: 82px;

    max-width: 100px;
  }
}

.promo__text {
  margin: 0 0 5px;

  line-height: 24px;

  @media (min-width: $tablet-width) {
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 20px;
  }
}

.promo__description {
  display: grid;
  margin: 0;

  line-height: 20px;

  grid-template-columns: 98px 1fr;

  @media (min-width: $tablet-width) {
    padding: 0;

    font-size: 15px;
  }
}

.promo__item {
  margin: 0;
  padding: 7px 0 12px;

  border-bottom: 1px solid #ececec;

  @media (min-width: $desktop-width) {
    padding: 5px 0 14px;
  }
}

.promo__image {
  width: 100%;
  max-width: 350px;
  height: 315px;
  margin: 12px auto;

  @media (min-width: $tablet-width) {
    width: 334px;
    height: 404px;
    margin-bottom: 0;

    grid-column: span 1/-1;
    grid-row: 2 / span 3;
  }

  @media (min-width: $desktop-width) {
    width: 525px;
    max-width: 100%;
    height: 636px;
    margin: 0;

    grid-row: 1 / span 5;
  }
}

.promo__image img {
  display: block;
  width: 100%;
  height: 100%;
}

.promo__price-container {
  order: 1;

  @media (min-width: $tablet-width) {
    padding-top: 20px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 74px;
  }
}

.promo__price {
  margin: 0 0 22px;

  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-align: center;

  @media (min-width: $tablet-width) {
    margin-bottom: 32px;

    font-size: 20px;
    line-height: 24px;
  }
}
