@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  display: grid;
  min-width: 320px;
  font-size: 15px;
  line-height: 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  grid-template-rows: auto 1fr auto;
}
@media (min-width: 768px) {
  body {
    font-size: 17px;
    line-height: 30px;
  }
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}

.button {
  display: block;
  width: 100%;
  padding: 14px 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: "Open Sans", "Arial", sans-serif;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
  border: 2px solid #444444;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    padding: 15px 20px 13px;
    font-size: 17px;
  }
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .button:hover, .button:focus {
    color: #ffffff;
    background-color: #444444;
    outline: none;
  }
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.title {
  margin: 0;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
.title--main {
  text-align: center;
}

.header {
  position: relative;
  background-color: #f1f1f1;
}
@media (max-width: 767px) {
  .header {
    padding-top: 80px;
  }
}
@media (min-width: 1150px) {
  .header {
    overflow-x: hidden;
  }
}

.header--home {
  background-color: #f9f9f9;
}

.nav {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  min-height: 79px;
  padding-top: 79px;
  background-color: #ffffff;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .nav {
    position: static;
    display: grid;
    padding: 0;
    background-color: #f1f1f1;
    border: none;
    gap: 19px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1150px) {
  .nav {
    gap: 0;
    grid-template-columns: 1fr 149px 1fr;
  }
}

@media (min-width: 768px) {
  .header--home .nav {
    background-color: #f9f9f9;
  }
}

.nav.container {
  padding-right: 0;
  padding-left: 0;
}

.nav__logo-container {
  position: absolute;
  top: 22px;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) and (max-width: 1149px) {
  .nav__logo-container {
    left: 48px;
    transform: none;
  }
}
@media (min-width: 1150px) {
  .nav__logo-container {
    position: static;
    padding-top: 10px;
    background-color: #ffffff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    transform: none;
    grid-column: 2/3;
    grid-row: 1/1;
  }
}

.nav__logo {
  display: block;
  width: 86px;
  height: 35px;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .nav__logo {
    width: 114px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .nav__logo {
    width: 149px;
    height: 72px;
  }
}

.nav__logo[href] {
  cursor: pointer;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .nav__logo[href]:hover, .nav__logo[href]:focus {
    outline: none;
    opacity: 0.6;
  }
}
.nav__logo[href]:active {
  opacity: 0.3;
}

.nav__logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.nav__toggle {
  position: absolute;
  top: 29px;
  right: 31px;
  z-index: 10;
  width: 20px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/svg/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__list {
  display: none;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (min-width: 768px) {
  .nav__list--user {
    justify-content: end;
    order: -1;
    padding-left: 200px;
    background-color: #ffffff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    grid-column: 1/-1;
  }
}
@media (min-width: 1150px) {
  .nav__list--user {
    position: relative;
    order: 0;
    padding-right: 15px;
    padding-left: 0;
    grid-column: 3/4;
  }
}
.nav__list--user::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: -1px;
  left: 100%;
  width: calc(50vw - 100%);
  background-color: #ffffff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

@media (min-width: 768px) {
  .nav__list--main {
    display: grid;
    justify-content: start;
    padding-left: 50px;
    gap: 38px;
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 1150px) {
  .nav__list--main {
    position: relative;
    padding-left: 15px;
    background-color: #ffffff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    gap: 0;
    grid-column: 1/2;
  }
}
.nav__list--main::before {
  content: "";
  position: absolute;
  top: 0;
  right: 100%;
  bottom: -1px;
  width: calc(50vw - 100%);
  background-color: #ffffff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.nav__list--promo {
  display: none;
}
@media (min-width: 1150px) {
  .nav__list--promo {
    display: grid;
    justify-content: start;
    padding: 16px 0 16px 50px;
    gap: 20px 63px;
    grid-column: 1/2;
    grid-template-columns: repeat(2, auto);
  }
}

.nav__item {
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .nav__item {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: none;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .nav__list--main .nav__item {
    border: none;
  }
}
@media (min-width: 1150px) {
  .nav__list--main .nav__item:first-child {
    border-left: none;
  }
}

@media (min-width: 1150px) {
  .nav__list--promo .nav__item {
    border: none;
  }
}

.nav__link {
  position: relative;
  display: block;
  padding: 30px 30px 25px 75px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .nav__link {
    display: inline-block;
    min-height: 87px;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .nav__link {
    padding: 33px 35px 31px 35px;
  }
}
@media (min-width: 768px) {
  .nav__link--icon {
    padding: 33px 49px 30px 82px;
  }
}
@media (min-width: 1150px) {
  .nav__link--icon {
    padding: 33px 35px 31px 85px;
  }
}

.nav__link[href] {
  cursor: pointer;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .nav__link[href]:hover, .nav__link[href]:focus {
    color: rgba(0, 0, 0, 0.6);
    outline: none;
  }
}
.nav__link[href]:active {
  opacity: 0.3;
}

.nav__link svg {
  position: absolute;
  top: 50%;
  left: 30px;
  width: 21px;
  height: 18px;
  transform: translateY(-50%);
  fill: rgba(0, 0, 0, 0.2);
}

.nav__link:hover svg,
.nav__link:focus svg {
  fill: #63d1bb;
}

@media (min-width: 768px) {
  .nav__link--search {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 33px 41px 30px;
  }
}

@media (min-width: 768px) {
  .nav__link--search span {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .nav__list--main .nav__link {
    min-height: auto;
    font-size: 15px;
    line-height: 24px;
  }
}

@media (min-width: 1150px) {
  .nav__list--promo .nav__link {
    min-height: auto;
    padding: 0;
    font-size: 15px;
    line-height: 24px;
  }
}

.nav__sale-text {
  display: none;
}
@media (min-width: 768px) {
  .nav__sale-text {
    display: inline-block;
    margin: 0 50px 0 auto;
    font-size: 15px;
    line-height: 24px;
    vertical-align: baseline;
  }
}
@media (min-width: 1150px) {
  .nav__sale-text {
    margin: 20px 50px 0 auto;
    grid-column: 3/4;
  }
}

@media (max-width: 767px) {
  .no-js .nav {
    position: static;
  }
}

@media (max-width: 767px) {
  .no-js .nav__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .no-js .nav__list--main,
.no-js .nav__list--user {
    display: block;
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__toggle {
    background-image: url("../img/svg/icon-menu-close.svg");
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__list--main,
.nav--opened .nav__list--user {
    display: block;
  }
}

.first-screen {
  padding: 73px 0;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .first-screen {
    padding: 53px 0 86px;
  }
}
@media (min-width: 1150px) {
  .first-screen {
    padding: 39px 0 112px;
  }
}
@media (min-width: 768px) {
  .first-screen::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    height: 24px;
    background-image: url("../img/svg/zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position: center;
    background-size: contain;
  }
}
@media (min-width: 1150px) {
  .first-screen::after {
    height: 34px;
    background-image: url("../img/svg/zigzag-fill-desktop.svg");
  }
}

.first-screen--home {
  position: relative;
  padding: 0;
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile.png");
  background-repeat: no-repeat;
  background-position: center top -17px;
}
@media (min-width: 768px) {
  .first-screen--home {
    background-image: url("../img/hero-tablet.png");
    background-position: center bottom 11px;
  }
}
@media (min-width: 1150px) {
  .first-screen--home {
    background-image: url("../img/hero-desktop.png");
    background-position: center bottom 17px;
  }
}

.first-screen--home .first-screen__container {
  display: flex;
  flex-direction: column;
  min-height: 488px;
  padding-top: 72px;
  padding-bottom: 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .first-screen--home .first-screen__container {
    min-height: 709px;
    padding-top: 106px;
  }
}
@media (min-width: 1150px) {
  .first-screen--home .first-screen__container {
    min-height: 700px;
    padding-top: 96px;
  }
}

@media (max-width: 767px) {
  .first-screen .first-screen__title {
    font-size: 27px;
  }
}

.first-screen__catalog-links {
  margin: auto -30px 0;
}
@media (min-width: 768px) {
  .first-screen__catalog-links {
    display: grid;
    margin: auto 0 0;
    grid-template-columns: repeat(2, 1fr);
  }
}

.first-screen__catalog-item {
  position: relative;
  display: block;
  padding: 24px 115px 31px 30px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  text-decoration: none;
  background-color: #63d1bb;
}
@media (min-width: 768px) {
  .first-screen__catalog-item {
    padding: 26px 115px 45px 36px;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .first-screen__catalog-item {
    padding-bottom: 50px;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .first-screen__catalog-item:hover, .first-screen__catalog-item:focus {
    background-color: #59d5bc;
    outline: 0;
    transform: translateY(-5px);
  }
}

.first-screen__catalog-item:nth-child(2n) {
  background-color: #56c3b2;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .first-screen__catalog-item:nth-child(2n):hover, .first-screen__catalog-item:nth-child(2n):focus {
    background-color: #46c1ae;
    outline: none;
  }
}

.first-screen__catalog-item span {
  display: block;
  max-width: 130px;
}

.first-screen__catalog-item svg {
  position: absolute;
  right: 36px;
  bottom: 20px;
  opacity: 0.5;
  fill: #149080;
}
@media (min-width: 768px) {
  .first-screen__catalog-item svg {
    bottom: 39px;
  }
}

.first-screen__catalog-item:active svg {
  opacity: 0.3;
}

.first-screen__catalog-item--toys svg {
  right: 27px;
}
@media (min-width: 768px) {
  .first-screen__catalog-item--toys svg {
    right: 33px;
  }
}

.promo {
  padding-top: 32px;
  padding-bottom: 42px;
  background-image: url("../img/svg/zigzag-line.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: contain;
}
@media (min-width: 768px) {
  .promo {
    padding-top: 40px;
    padding-bottom: 67px;
  }
}
@media (min-width: 1150px) {
  .promo {
    padding-top: 54px;
    padding-bottom: 12px;
    background: none;
  }
}

.promo__container {
  display: grid;
  gap: 16px;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .promo__container {
    display: grid;
    gap: 37px 42px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1150px) {
  .promo__container {
    position: relative;
    padding-bottom: 60px;
    padding-left: 226px;
    background: none;
    gap: 35px 60px;
    grid-template-columns: 1fr 525px;
  }
}
@media (min-width: 1150px) {
  .promo__container::after {
    content: "";
    position: absolute;
    right: 50px;
    bottom: 0;
    left: 50px;
    z-index: 1;
    height: 23px;
    background-image: url("../img/svg/zigzag-line.svg");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: contain;
  }
}

@media (min-width: 768px) {
  .promo__title-container {
    display: grid;
    align-items: center;
    gap: 10px;
    grid-column: 1/-1;
    grid-template-columns: 1fr 125px;
  }
}
@media (min-width: 1150px) {
  .promo__title-container {
    padding-top: 62px;
    grid-row: 1/span 1;
  }
}

.promo__title {
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .promo__title {
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .promo__title {
    z-index: 1;
  }
}

.promo__type {
  display: block;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__type {
    line-height: 17px;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .promo__type {
    position: absolute;
    top: 70px;
    left: 82px;
    max-width: 100px;
  }
}

.promo__text {
  margin: 0 0 5px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .promo__text {
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .promo__text {
    margin-bottom: 20px;
  }
}

.promo__description {
  display: grid;
  margin: 0;
  line-height: 20px;
  grid-template-columns: 98px 1fr;
}
@media (min-width: 768px) {
  .promo__description {
    padding: 0;
    font-size: 15px;
  }
}

.promo__item {
  margin: 0;
  padding: 7px 0 12px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 1150px) {
  .promo__item {
    padding: 5px 0 14px;
  }
}

.promo__image {
  width: 100%;
  max-width: 350px;
  height: 315px;
  margin: 12px auto;
}
@media (min-width: 768px) {
  .promo__image {
    width: 334px;
    height: 404px;
    margin-bottom: 0;
    grid-column: span 1/-1;
    grid-row: 2/span 3;
  }
}
@media (min-width: 1150px) {
  .promo__image {
    width: 525px;
    max-width: 100%;
    height: 636px;
    margin: 0;
    grid-row: 1/span 5;
  }
}

.promo__image img {
  display: block;
  width: 100%;
  height: 100%;
}

.promo__price-container {
  order: 1;
}
@media (min-width: 768px) {
  .promo__price-container {
    padding-top: 20px;
  }
}
@media (min-width: 1150px) {
  .promo__price-container {
    padding-top: 74px;
  }
}

.promo__price {
  margin: 0 0 22px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__price {
    margin-bottom: 32px;
    font-size: 20px;
    line-height: 24px;
  }
}

.about {
  padding: 32px 0;
}
@media (min-width: 768px) {
  .about {
    padding-top: 58px;
    padding-bottom: 54px;
  }
}

@media (min-width: 1150px) {
  .about__container {
    padding: 0 225px 0;
  }
}

.about__list {
  padding-top: 27px;
}
@media (min-width: 768px) {
  .about__list {
    padding-top: 41px;
    column-count: 2;
    column-gap: 0;
  }
}
@media (min-width: 1150px) {
  .about__list {
    padding-top: 38px;
  }
}

.about__item {
  margin-bottom: 28px;
  padding-top: 55px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .about__item {
    max-width: 325px;
    margin-bottom: 54px;
    padding-top: 57px;
    font-size: 20px;
    line-height: 22px;
  }
}
.about__item:last-child {
  margin-bottom: 0;
}
.about__item--ecolog {
  background-image: url("../img/svg/icon-ecolog.svg");
  background-position: top left 8px;
}
.about__item--rusprice {
  background-image: url("../img/svg/icon-rusprice.svg");
  background-position: top left;
}
.about__item--like {
  background-image: url("../img/svg/icon-like.svg");
  background-position: top 6px left 7px;
}
.about__item--handmade {
  background-image: url("../img/svg/icon-handmade.svg");
  background-position: top 3px left 4px;
}
.about__item--support {
  background-image: url("../img/svg/icon-support.svg");
  background-position: top 4px left -2px;
}
.about__item--gift {
  background-image: url("../img/svg/icon-gift.svg");
  background-position: top 2px left 3px;
}

.reviews {
  padding-top: 25px;
  padding-bottom: 51px;
  background-image: url("../img/svg/zigzag-line.svg");
  background-repeat: no-repeat;
  background-position: center bottom;
