.form__input {
  width: 100%;
  max-width: 700px;
  padding-top: 11px;
  padding-bottom: 14px;
  font-weight: 400;
  font-size: 17px;
  line-height: 18px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #000000;
  appearance: none;
  /* stylelint-disable */
  /* stylelint-enable */
}
@media (min-width: 768px) {
  .form__input {
    padding: 12px 17px;
    font-size: 19px;
  }
}
@media (min-width: 1150px) {
  .form__input {
    padding: 14px 17px 15px;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .form__input:hover, .form__input:focus {
    border-color: #62d1ba;
    outline: none;
  }
}
.form__input:disabled {
  border-color: #000000;
  opacity: 0.3;
}
.form__input--error {
  border-color: #ff0000;
}
.form__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input:-moz-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input::-moz-placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}
.form__input::-webkit-outer-spin-button, .form__input::-webkit-inner-spin-button {
  margin: 0;
  -webkit-appearance: none !important;
}
.form__input[type=number] {
  -moz-appearance: textfield;
}

@media (min-width: 768px) {
  .form__section--contacts {
    padding-top: 48px;
    padding-bottom: 26px;
  }
}
@media (min-width: 1150px) {
  .form__section--contacts {
    padding-bottom: 29px;
  }
}

.form__section--contacts .form__container {
  padding-top: 17px;
  padding-bottom: 5px;
}
@media (min-width: 768px) {
  .form__section--contacts .form__container {
    padding-top: 0;
    padding-bottom: 23px;
  }
}

.form__section--contacts .form__input {
  padding: 5px 45px 15px 0;
}
@media (min-width: 768px) {
  .form__section--contacts .form__input {
    padding: 11px 75px 14px 17px;
  }
}

.form__label-contacts {
  position: relative;
  display: block;
}
@media (min-width: 1150px) {
  .form__label-contacts {
    max-width: 700px;
  }
}
.form__label-contacts::after {
  content: "";
  position: absolute;
  top: 2px;
  display: block;
  background-color: rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .form__label-contacts::after {
    top: 15px;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .form__label-contacts:hover::after, .form__label-contacts:focus-within::after {
    background-color: #62d1ba;
  }
}
.form__label-contacts:active::after {
  background-color: #62d1ba;
}

.form__label-contacts--tel::after {
  right: 8px;
  width: 25px;
  height: 24px;
  mask-image: url("../img/svg/icon-phone.svg");
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__label-contacts--tel::after {
    right: 27px;
  }
}

.form__label-contacts--mail::after {
  right: 2px;
  width: 32px;
  height: 21px;
  mask-image: url("../img/svg/icon-mail.svg");
}
@media (min-width: 768px) and (max-width: 1149px) {
  .form__label-contacts--mail::after {
    right: 22px;
  }
}

.form__section--message {
  padding-top: 17px;
  padding-bottom: 10px;
}
@media (min-width: 768px) {
  .form__section--message {
    padding: 0;
  }
}

.form__section--message textarea {
  height: 142px;
  margin-top: 7px;
  padding: 15px;
  border: 2px solid #444444;
  resize: none;
}
@media (min-width: 768px) {
  .form__section--message textarea {
    margin-top: 0;
  }
}

.form__section--submit .form__container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .form__section--submit .form__container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 3px;
  }
}
@media (min-width: 1150px) {
  .form__section--submit .form__container {
    padding: 2px 225px 50px;
  }
}

.form__hint {
  order: -1;
  margin: 0 0 28px;
  font-size: 14px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .form__hint {
    order: 0;
    margin: 0;
    font-size: 17px;
    line-height: 30px;
  }
}

.form__button {
  padding: 16px 20px;
  font-size: 17px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .form__button {
    width: 230px;
  }
}

.footer {
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .footer {
    padding-top: 51px;
    padding-bottom: 57px;
  }
}

@media (min-width: 768px) {
  .footer__wrapper {
    display: grid;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    grid-template-columns: repeat(3, auto);
  }
}

.footer__logo {
  display: none;
  width: 102px;
  height: 34px;
  color: #000000;
}
@media (min-width: 768px) {
  .footer__logo {
    display: block;
  }
}

.footer__logo[href] {
  cursor: pointer;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .footer__logo[href]:hover, .footer__logo[href]:focus {
    color: #46c1ae;
    outline: none;
  }
}
.footer__logo[href]:active {
  opacity: 0.3;
}

.footer__logo svg {
  fill: currentColor;
}

.social {
  display: grid;
  justify-content: center;
  padding: 27px 0 31px;
  border-bottom: 1px solid #e5e5e5;
  gap: 27px;
  grid-template-columns: repeat(3, auto);
}
@media (min-width: 768px) {
  .social {
    padding: 0;
    border: none;
  }
}

.social__link {
  display: block;
  width: 28px;
  height: 22px;
  color: #231f20;
  cursor: pointer;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .social__link:hover, .social__link:focus {
    color: #46c1ae;
    outline: none;
  }
}
.social__link:active {
  opacity: 0.3;
}

.social__link svg {
  fill: currentColor;
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 21px;
  padding-bottom: 20px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .copyright {
    padding: 0;
  }
}
.copyright:focus {
  outline: none;
}

.copyright__text {
  margin-right: 27px;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: #000000;
}

.copyright svg {
  fill: #63d1bb;
}

.copyright:hover svg,
.copyright:focus svg {
  fill: #46c1ae;
}

.copyright:active svg {
  opacity: 0.3;
}

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: none;
  background-color: rgba(255, 255, 255, 0.9);
}

.overlay--opened {
  display: block;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 20;
  display: none;
  max-width: 566px;
  background: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  transform: translate(-50%, -50%);
}

.modal--opened {
  display: block;
}

.size {
  width: 320px;
  padding: 8px 30px 46px;
}
@media (min-width: 768px) {
  .size {
    width: 566px;
    padding: 12px 106px 46px;
  }
}

.size__title {
  margin-bottom: 14px;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .size__title {
    margin-bottom: 11px;
    font-size: 30px;
    line-height: 57px;
  }
}

.size__text {
  margin: 0 0 22px;
  font-size: 15px;
  line-height: 30px;
  text-align: center;
  color: #444444;
}
@media (min-width: 768px) {
  .size__text {
    margin-bottom: 20px;
    font-size: 17px;
  }
}

.size__container {
  display: grid;
  margin-bottom: 31px;
  gap: 7px;
  grid-template-columns: repeat(3, 1fr);
}

.size__label {
  display: block;
  width: 82px;
  padding: 17px 10px 15px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  text-align: center;
  color: #444444;
  border: 2px solid rgba(68, 68, 68, 0.1);
}
@media (min-width: 768px) {
  .size__label {
    width: 112px;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .size__label:hover, .size__label:focus {
    border-color: rgba(68, 68, 68, 0.3);
    outline: none;
  }
}
.size__label:active {
  opacity: 0.6;
}

.size__input:checked + .size__label,
.size__input:focus + .size__label {
  border: 2px solid #63d1bb;
}

.size__input:disabled + .size__label {
  border: 2px solid #444444;
  opacity: 0.6;
}

.size__submit {
  padding: 16px 20px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  background-color: #63d1bb;
  border: 2px solid #63d1bb;
}